webpackJsonp([30909594585211],{

/***/ 457:
/***/ (function(module, exports) {

	module.exports = {"pathContext":{"posts":[{"html":"<h1>动机</h1>\n<h2>应用需求</h2>\n<p>因为笔者在外企工作，英语是第二工作语言，不断学习、提高英语水平成了一个必不可少的日常。而在英语学习中，一个很重要的环节就是背单词，是的，很枯燥的那个背单词。</p>\n<p>对一般人来说，背单词的第一目的是<strong>认识单词</strong>，也就是在阅读英文文章、看邮件的时候碰到单词能知道什么意思，但是在一个外企工作环境里面，参加各种线上线下会议，能听懂别人说的也很重要，所以，背过的单词还需要<strong>能听懂</strong>，当然，最终还需要能用得上，用准确的词汇来描述事情，也就是要能<strong>输出</strong>。 输出的过程比较复杂，需要有受众，需要交互，比较难于程序化。</p>\n<p>市场的有很多的背单词软件，也有很多<ruby>创新<rt>变形</rt></ruby>的通过阅读、朗诵、听力等等来达到背单词效果的应用，可用过一些之后，或多或少都有点这样那样的问题。因为本篇重点是介绍技术的文章，所以不详细对比各家软件的优劣及各自问题了。</p>\n<p>我心目中理想的背单词软件是：</p>\n<ul>\n<li>好的遗忘算法。背单词就是不断重复，什么时候重复很重要，<a href=\"https://en.wikipedia.org/wiki/Spaced_repetition\">Spaced Repetition</a>遗忘算法在这个领域很流行，万能的记忆辅助软件Anki就是基于这个算法。</li>\n<li>权威的内容。很多单词软件中的单词解释都断章取义，或者只给简单的中文解释，这就造成很多时候用词不准确，因为你背的单词中文解释太笼统。需要有英文解释，有例句。比如：<code>meeting</code>, 简单的背单词软件只是给你个中文翻译：<code>会议、会合</code>，于是有人在翻译<code>公司年会</code>的<code>会</code>时，第一个想到的就是<code>company meeting</code>，哈哈 。</li>\n<li>有语音。语音一则可以验证自己的发音是否准确，另外可以用来向耳朵单向输入。当然，语音最好能是完整的句子，这样才能知道如何应用。</li>\n<li>充分利用整段时间结合零碎时间。可以在整段时间背诵，也很好地可以利用坐公交、上大号等零碎时间。</li>\n</ul>\n<p>基于此，就有了自己开发一个背单词应用的想法，随后就有了《世凝听记》。</p>\n<h2>技术需求</h2>\n<h3>前端</h3>\n<p>说完功能上的需求，需要再考虑一下技术方面的情况。总的来说，我希望开发一个手机上的应用，而不是电脑端的，毕竟现代人已经离不开手机，而且手机功能也足够强大。</p>\n<p>手机应用又可以细分，到底是开发个原生的app,还是用类似React Native开发个可以跨平台的，然后分别发布到安卓和苹果应用商店？不过感觉都太麻烦了，最后决定还是选择微信小程序吧，主要原因：</p>\n<ul>\n<li>小程序天然跨平台，同时支持安卓、苹果</li>\n<li>免去各种应用商店发布上架的麻烦，毕竟一个人无法对接那么多的商店</li>\n<li>\n<p>小程序的基本功能也足够，毕竟我们需要的只是：</p>\n<ul>\n<li>简单页面</li>\n<li>语音播放</li>\n<li>后台交互</li>\n</ul>\n</li>\n</ul>\n<h3>后端</h3>\n<p>后端需要实现的功能可以简单概括为：</p>\n<ul>\n<li><a href=\"https://ankiweb.net\">Anki</a>集成（可以实现背单词，还不知道什么是Anki的请自行问度娘）</li>\n<li>单词语音制作、历史文件的存储 （订制单词语音）</li>\n<li>简单的用户管理</li>\n</ul>\n<p>开发后端第一个冒出来的想法是需要个服务器，可随便哪个平台，哪怕租个最低配的服务器每年也得上千的成本，对于这个以自用为出发点的小应用成本太高了。一些免费的PAAS比如Salesforce的Heroku倒是可以考虑，但是Heroku生态里面的服务也比较有限，而且还考虑将来如果万一用户量多了，Heroku扩展也稍差了些。</p>\n<p>最后，还是选择了AWS，因为开发这个应用需要：</p>\n<ul>\n<li>无服务器托管后台代码，那自然是选择AWS <code>Lambda</code>。</li>\n<li>提供一个API，供小程序调用，所以，我们需要AWS <code>API Gateway</code>。</li>\n<li>文字转语音服务，试了一下AWS <code>Polly</code>，感觉也不错。</li>\n<li>语音文件的存储，开始可能文件比较少，但是如果用户量增大，系统的扩展就是个挑战，在AWS上文件管理当然是<code>S3</code>。</li>\n<li>用户信息管理。一提到<strong>信息管理</strong>，自然而然就会想到数据库，可数据库服务一般都是最昂贵的部分，咱这个人爱好可别被AWS <code>RDS</code>账单上N位数金额给打击喽。仔细想想本着一个绿色软件的开发原则，我们也不需要用户注册等步骤，只要是微信用户就可以使用，也不需要了解你的个人信息，所以决定这个部分也用<code>S3</code>来解决。</li>\n</ul>\n<h1>技术实现</h1>\n<h2>架构</h2>\n<img src=\"data:image/png;base64,\" height=\"300\"/>\n<p>这其实也谈不上什么架构，太简单了：小程序发送API请求给API Gateway，API Gateway转发给Lambda，Lambda调用Polly API创建语音，然后Lambda将创建好的语音存储到S3。</p>\n<h2>组件描述</h2>\n<p>下面分开介绍一下每一种技术，以及在使用过程中遇到的一些问题及解决办法。</p>\n<h3>AWS Polly</h3>\n<p>《世凝听记》最开始也是最核心的需求，就是将文字转换成语音，AWS Polly就是这样一个服务。Polly的使用非常的简单，就是普通的API的调用，加上一些简单的Speech Synthesis Markup Language（SSML）知识。</p>\n<p>引入包：</p>\n<pre><code>&#x3C;dependency>\n    &#x3C;groupId>com.amazonaws&#x3C;/groupId>\n    &#x3C;artifactId>aws-java-sdk-polly&#x3C;/artifactId>\n    &#x3C;version>1.11.77&#x3C;/version>\n&#x3C;/dependency>\n</code></pre>\n<p>因为语音中需要来回切换中英文两种语言，而Polly现在还无法做到自动无缝地切换两种语言，所以只能通过元数据来提示，这就是<a href=\"https://docs.aws.amazon.com/polly/latest/dg/ssml.html\">SSML</a></p>\n<p>为了方便，我将这些做了简单封装，查看 <a href=\"https://github.com/smilingleo/moerdo-english/blob/master/src/main/java/leo/me/polly/Polly.java\">Polly.java</a>, 使用的时候只需要调用：</p>\n<pre><code class=\"language-java\">Polly polly = new Polly(config);\npolly.encodeEnglish(\"Hi there, I'm Leo, glad to meet you!\");\npolly.encodeChinese(\"大家好，我叫大宝，很高兴见到大家！\");\n</code></pre>\n<p>之后就是<strong>区分中英文</strong>，调用相应API了。识别文字的语言大概可以通过字符的编码来进行，汉字字符集在Unicode中的编码有一个范围：</p>\n<pre><code class=\"language-java\">public static boolean isChinese(char c) {\n    Character.UnicodeBlock ub = Character.UnicodeBlock.of(c);\n    if (ub == Character.UnicodeBlock.CJK_UNIFIED_IDEOGRAPHS\n            || ub == Character.UnicodeBlock.CJK_COMPATIBILITY_IDEOGRAPHS\n            || ub == Character.UnicodeBlock.CJK_UNIFIED_IDEOGRAPHS_EXTENSION_A\n            || ub == Character.UnicodeBlock.GENERAL_PUNCTUATION\n            || ub == Character.UnicodeBlock.CJK_SYMBOLS_AND_PUNCTUATION\n            || ub == Character.UnicodeBlock.HALFWIDTH_AND_FULLWIDTH_FORMS) {\n        return true;\n    }\n    return false;\n}\n</code></pre>\n<p>比如，单词<code>accused</code>有一个例句：</p>\n<pre><code>例：The fifteen accused, young men from different parts of England, denied the charges.被告是15名来自英格兰各地的年轻男子，他们对指控矢口否认。\n</code></pre>\n<p>英文句子夹在中文之间，那么就需要通过上面工具来识别中英文，可以将英文过滤掉只剩下中文，然后调用<code>polly.encodeChinese(...)</code>，然后再把中文过滤掉，剩下英文，之后调用<code>polly.encodeEnglish(...)</code>。</p>\n<h3>AWS Lambda</h3>\n<p>Lambda是函数式编程里重要的一个概念，AWS把他们的产品命名为Lambda，一定程度上，也是让大家直观地将一个Lambda抽象为一个Function。FP里面很注重没有副作用，最常见的副作用就是I/O。有点编程经验的人都知道，I/O是无法避免的，所以AWS把所有I/O相关的部分也都剥离为单独的服务，比如数据库RDS，Dynamo，对象存储S3等等，剥离了这些之后，Lambda本身就是没有副作用的了（宏观意义上，变量级别side effect是微观意义上的，这里不需要较真）。</p>\n<p>和FP里面的副作用扯上关系有点牵强，不过说Lambda是stateless应该没问题。正因为无状态，你的代码才可以被自动的部署/运行/下架，而与计算环境无关，可以在任何一个Runtime上运行。<strong>真正做到按需付费!</strong></p>\n<p>当然，这都是有代价的。代价就是如果你的lambda在没有负载的情况下，会被停止运行，新请求来了的时候，需要先部署、启动你的服务，这就意味着你的API性能波动会比较大。如果你的API响应时间是<code>100ms</code>，而你的服务启动时间需要<code>3s</code>，那么一个请求可能就需要超过<code>3100ms</code>的响应时间。这也就意味着，最好不要用很臃肿的开发框架，比如你开发个lambda费要用上SpringBoot，之后你就得头疼性能问题了。</p>\n<p>使用Lambda最大的好处是：你每个月可以享受一百万次请求的免费额度，超过才付费。如果你需要更快的响应时间，那你可以配置你的lambda一定的预留concurrency，这样系统响应会更快，但是也就意味着你会多花钱。</p>\n<p>AWS Lambda支持的开发语言很多，我用的Java，主要是考虑Java操作字节码及Stream相比Node容易些。</p>\n<p>实现一个Lambda很简单，只需要定义一个class，实现<code>com.amazonaws.services.lambda.runtime.RequestHandler</code>接口，比如：</p>\n<pre><code class=\"language-java\">package example;\nimport com.amazonaws.services.lambda.runtime.Context;\nimport com.amazonaws.services.lambda.runtime.RequestHandler;\n\npublic class Hello implements RequestHandler&#x3C;Integer, String>{\n  public String handleRequest(Integer myCount, Context context) {\n    return String.valueOf(myCount);\n  }\n}\n</code></pre>\n<p>将代码打包为jar，上传到AWS Lambda，然后指定Handler的方法名：<code>example.Hello::handleRequest</code>就好了。</p>\n<p>关于调试，你常用的调试技巧依旧有效，你可以通过写log，log内容会被AWS CloudWatch记录下来。当然，你也可以在本地通过单元测试队这些代码进行调试，这样你就可以设断点跟踪，你可能需要Mock一些服务，总之，写容易测试的代码会让你的调试变得简单。</p>\n<p>关于Lambda的粒度，有人会将Lambda拆分的很细，当然这与你的代码模块化有关，可以每一个API创建一个Lambda，这样的好处就是你可以灵活地定义不同API的性能参数；升级一个API的时候不会影响到其他的API。如果需要整合这些小粒度API，提供一个更大的功能，可以使用AWS Step Function，将这些小的API像工作流一样组合起来，相当灵活。</p>\n<p>当然，灵活的同时也带来了一定的运维负担。</p>\n<p>对于本项目这种简单的应用，我只是简单地将所有API合并在一个Lambda里了，通过一个<code>command</code>参数来区分是哪个API。当然，这也是因为微信小程序在调用后台服务的时候采取的保守的做法，只能调用预定义的URL。</p>\n<h3>AWS S3</h3>\n<p>S3（Simple Storage Service）可以算是AWS最成功的服务了，号称可以在任何时候，任何地点，存取任意大小的文件，不过确实很方便。</p>\n<p>在本系统里，S3的目录结构如下：</p>\n<pre><code>users                // bucket\n +--&#x3C;wechatId>       // 每个用户一个独立的folder\n   +-- userInfo.json //用户信息，包含wechatId, 创建时间，上次登录时间，剩余点数（用来防止滥用），偏好等。\n   +--audio  \n     +-- &#x3C;timestamp>.mp3 // 历史音频文件\n     +-- &#x3C;timestamp>.txt // 历史音频对应的单词\n</code></pre>\n<p>如果需要读取某个用户的信息，或者查看其语音制作历史，仅仅需要知道用户的wechatId，然后访问相应的文件就可以了。这种用法基本上等同于将S3当作一个文档数据库在用。</p>\n<p>读取用户信息的代码片段：</p>\n<pre><code class=\"language-java\">String userInfoPath = ... // 组装userInfo的路径信息\nS3Object object = null;\ntry {\n    object = s3Client.getObject(USER_BUCKET_NAME, userInfoPath);\n    // 从S3Object中读取文件内容为字符串\n    String jsonString = IOUtils.toString(object.getObjectContent());\n    // 字符串反序列化为Object\n    userInfo = objectMapper.readValue(jsonString, UserInfo.class);\n    userInfo.setLastLogin(now.toString());\n} catch (IOException e) {\n    throw new IllegalStateException(\"Invalid userInfo.json content found.\", e);\n} finally {\n    IOUtils.closeQuietly(object, log);\n}\n</code></pre>\n<p>另外一个重要的操作是写文件到S3。前文中我们知道，<code>polly.encodeEnglish(...)</code>返回是字节数组，也就是音频文件的字节编码，保存文件就是需要将这些字节写入到S3中：</p>\n<pre><code class=\"language-java\">// 音频字节码\nbyte[] audioBytes = polly.encodeChinese(\"hello world\");\n\n// 创建写入S3的InputStream, 作为PutObjectRequest的构造参数。\ntry (ByteArrayInputStream baIn = new ByteArrayInputStream(audioBytes)) {\n    // 文件元数据，下载文件的时候会有用\n    ObjectMetadata metadata = new ObjectMetadata();\n    metadata.setContentType(\"audio/mpeg3\");\n    metadata.addUserMetadata(\"x-amz-meta-title\", format(\"%s.mp3\", timeStamp));\n    metadata.setContentLength(audioBytes.length);\n\n    final String fileName = format(\"%s/audio/%s.mp3\", wechatId, timeStamp);\n    log.info(format(\"writing file: %s, size: %d\", fileName, audioBytes.length));\n    // 构建写入请求\n    PutObjectRequest putRequest = new PutObjectRequest(USER_BUCKET_NAME, fileName, baIn, metadata);\n    // 写入数据\n    s3Client.putObject(putRequest);\n}\n</code></pre>\n<h3>AWS API Gateway</h3>\n<p>在创建Lambda的时候，需要指定如何来触发这个function，这里我们当然是用API Gateway了。下面就是一个用API Gateway定义一个API，调用Lambda的流程示意图。</p>\n<img src=\"https://docs.aws.amazon.com/apigateway/latest/developerguide/images/apigateway-get-method-execution-boxes.png\" height=\"360\">\n<p>从上图中可以看到，在一个请求从进入API Gateway之后，到发送到Lambda之前，有两个环节Method Request, Integration Request，我们可以响应地定义API的请求格式、身份验证、参数等等，还可以对request body进行简单处理。</p>\n<p>而在Lambda返回结果，到返回客户端之间，也有两个环节：Integration Response, Method Response，这两个在本应用中比较重要。</p>\n<p>从Lambda的定义我们可以看出，Lambda只能返回一个类型的response，那么问题来了：如何在碰到不同类型的Exception的时候返回不同的HTTP Response Code呢？ 比如，如果碰到InvalidArgumentException，应该是客户端发送的请求有格式等问题，理应返回400错误，而服务器端错误应该返回500，但是Lambda怎么让API Gateway知道？</p>\n<p>在刚开始用Gateway的时候，我确实遇到了这个问题，所有的API Response Code都是200，结果导致在微信小程序端的错误处理不起作用。查看文档之后才发现这个问题。解决办法其实也简单：根据Lambda返回内容做正则匹配，如果有<code>errorMessage</code> field（注意，必须是这个名字），那就会匹配<code>Integration Response</code>中定义的正则规则，比如，如果<code>errorMessage</code>是以<code>ServerSideException</code>开头，那就返回<code>500</code>。我们就可以配置为：<code>Lambda Error Regex: ServerSideException.*</code>。</p>\n<p>API Gateway很灵活，能做很多扩展，但是价格相对较贵，如果我们只是简单的API，可以使用AWS新的HTTP API。</p>\n<h3>微信小程序</h3>\n<p>微信小程序的开发教程网上太多了，我这里就不多说了。</p>\n<p>我只是简单仿造官方的组件展示小程序，照猫画虎就好了。只是在开发过程中需要自己找下图标素材，调试一下CSS样式，让页面更好看、更符合常人的使用习惯些。</p>\n<p>微信小程序作为微信的一种功能扩展，本质上是在微信的进程中提供了一个运行第三方插件运行的Runtime，这个本质也决定了微信小程序的优缺点：简单，但功能受限。</p>\n<p>如果有React, Vue，Angular等MVVM框架的使用经验，小程序开发上手十分容易。不过想做一些相对复杂些的功能就比较受限了，连最基本的在界面上选择文字这种操作都需要特殊的操作。</p>\n<p>让我最不能接受的是，纵观整个小程序开发官方文档，对于单元测试几乎算是完全忽略了，只能找到<a href=\"https://developers.weixin.qq.com/miniprogram/dev/framework/custom-component/unit-test.html\">单元测试</a>章节，跟着介绍走完结果发现根本行不通。而且更加不可理解的是：搜索了网上很多小程序样例代码库，居然没有找到一个包含单元测试的例子！！！管中窥豹，由此可见腾讯对于代码质量的价值观是什么样的。</p>\n<p>小程序的发布需要先通过审核，审核的同学会帮你做简单的sanity测试，也算是一个小福利。</p>\n<p>小程序后台还提供访问流量统计信息，也是一个相比开发native app的好处之一。</p>\n<h3>Anki</h3>\n<p>集成Anki主要有两个技术问题：</p>\n<ol>\n<li>如何解析Anki卡组</li>\n<li>如何调用AnkiWeb API</li>\n</ol>\n<p>Anki卡组包解压后其实就是一个SqlLite数据库，库表结构可以参考<a href=\"https://decks.fandom.com/wiki/Anki_APKG_format_documentation\">这个文章</a>。重要的是两个表：note（单词）和card（记录选项历史等信息）。</p>\n<p>所有的Anki客户端都需要和AnkiWeb同步，所以才可以实现跨平台，多种客户端一起用。所以，我们可以直接集成AnkiWeb的后台系统。</p>\n<p>AnkiWeb后台的几个API如下：</p>\n<h4>登录</h4>\n<p>登录需要首先访问登录页面，解析html来获取CSRF令牌，之后就可以通过：</p>\n<pre><code>curl -i -H \":authority: ankiweb.net\" -H \":method: POST\" -H \":path: /account/login\" -H \":scheme: https\" -H \"x-requested-with: XMLHttpRequest\" https://ankiweb.net/account/login -d submitted=1 -d csrf_token=&#x3C;csrf_token> -d username=&#x3C;username> -d password=&#x3C;password>\n</code></pre>\n<p>成功之后会设置<code>ankiweb</code>cookie，用于后续API。</p>\n<h4>列出所有卡组</h4>\n<pre><code>curl -i -H \"cookie: ankiweb=&#x3C;cookie>\" -H \":authority: ankiweb.net\" -H \":method: GET\" -H \":path: /decks/\" -H \":scheme: https\" -X GET https://ankiweb.net/decks/\n</code></pre>\n<h4>选择卡组</h4>\n<p>在背单词之前，必须先选中一个卡组，这也是在anki各种客户端之间同步的时候，只运行一个会话的原因。</p>\n<pre><code>curl -i -H \"cookie: ankiweb=&#x3C;cookie>\" -H \":authority: ankiweb.net\" -H \":method: POST\" -H \":path: /decks/select/&#x3C;deck_id>\" -H \":scheme: https\" -H \"x-requested-with: XMLHttpRequest\" https://ankiweb.net/decks/select/&#x3C;deck_id>\n</code></pre>\n<h4>获取卡牌（同时提交上批卡牌的答案）</h4>\n<pre><code>curl -i \\\n    -H \"cookie: ankiweb=&#x3C;cookie>\"\\\n    -H \":authority: ankiuser.net\"\\\n    -H \":method: POST\"\\\n    -H \":path: /study/getCards\"\\\n    -H \":schemested-with: XMLHttpRequest\"\\\n    -X POST https://ankiuser.net/study/getCards -d answers=[] -d ts=1577326449902\n</code></pre>\n<p><code>answers</code>参数例子：</p>\n<pre><code>answers: [\n    [\n        1468852725069,  // card id\n        3,  // answer index\n        845294 // the duration in milli-seconds starting from the card is shown to the moment it's answered.\n    ],\n    [1468852727608,3,11486],[1468852726783,2,41176],[1468852725289,2,25019],[1468852727583,3,15638]\n]\n</code></pre>\n<p>这个API算是一石二鸟，不过总觉得API的命名上有问题：<code>POST /study/getCards</code>，POST是写，getCards是读，组合在一起就感觉莫名其妙了。</p>\n<h1>成本</h1>\n<p>从本小程序发布到现在，有一个多月了，下面是我在AWS上的账单：</p>\n<img src=\"data:image/png;base64,\" height=\"480\">\n<p>是的，你没有看错，一个月<strong>2美分</strong>！！！</p>\n<p>账单全是S3的API调用开销。当然，这基本上只是自己一个人用的成本，如果用的人多了，超过了那些free tiers，成本还是会上来的。也是基于此，在小程序方面也做了一些简单的限制，如果超过一定用量，会被限制。</p>\n<h1>心得体会</h1>\n<ol>\n<li>\n<p>兴趣驱动</p>\n<p>整个开发完全是兴趣驱动，如果没有兴趣可能不会涉猎微信小程序的开发以及像AWS Polly等服务的学习。</p>\n<p>所以，兴趣是帮助你提高的初始动力。</p>\n</li>\n<li>\n<p>全栈的胜利</p>\n<p>整个应用端到端的开发用了大概一周的时间，API设计、界面设计、服务实现，如果不是全栈，可能你就得找到志同道合的的互补方（估计一般是前端），花大量时间沟通，尝试并行开发，然后联调，效率肯定远远没有全栈高。</p>\n<p>不过效率高是不是就一定好？沟通过程是否能够碰撞出更多的火花？或者发现设计上的潜在问题？这些问题可能永远没有绝对的对或者错，只能是合适的人用合适的方法才能有更好的产出。</p>\n</li>\n</ol>\n<p>最后，欢迎大家试用《世凝听记》。</p>\n<img src=\"data:image/jpeg;base64,\" height=\"200\">\n<p>附上<a href=\"https://mp.weixin.qq.com/s/QV-xDqnKYuazWt2M7b0UJQ\">《世凝听记使用指南》</a>，及<a href=\"https://mp.weixin.qq.com/s/_l1eZFA9h-O5dzIzPp9hFw\">《1.0版本新特性》</a></p>","id":"/Users/lliu/github/smilingleo.github.io/src/pages/2020/如何打造一个serverless的微信小程序.md absPath of file >>> MarkdownRemark","frontmatter":{"date":"2020-01-11T13:11:29.000+08:00","path":"/2020/how-to-build-a-serverless-wechat-mini-program","title":"如何打造一个serverless的微信小程序","excerpt":"本文介绍了一个完全serverless的应用开发过程以及所用技术的简单介绍。","tags":["serverless","微信小程序","AWS","Anki","背单词"]}}],"tagName":"serverless"}}

/***/ })

});


